import React from "react";
import GoogleMapReact from "google-map-react";

import { Icon } from "@iconify/react";
import locationIcon from "@iconify/icons-mdi/map-marker";

export default function GoogleMaps({ getLocationList, docList, pageIndex }) {
    const [docLocationList, setDocLocationList] = React.useState(null);
    const [pinIndex, setPinIndex] = React.useState(0);
    React.useEffect(() => {
        async function getList() {
            const list = await getLocationList(docList[pageIndex]);
            setDocLocationList(list);
        }
        getList();
    }, [pageIndex]);

    const defaultCenter =
        docLocationList && docLocationList.length
            ? docLocationList[0]
            : { lat: -37, lng: 122 };
    const defaultZoom = 8;

    if (docLocationList && docLocationList.length) {
        return (
            <GoogleMapReact
                bootstrapURLKeys={{
                    key: "AIzaSyDNTiDyjpRmR_wcSHZYb0j4MEkHTtjWLbM",
                }}
                defaultCenter={defaultCenter}
                defaultZoom={defaultZoom}
            >
                {docLocationList.map((marker, index) => (
                    <LocationPin
                        lat={marker.lat}
                        lng={marker.lng}
                        text={marker.location}
                        name={marker.name}
                        index={index}
                        setPinIndex={setPinIndex}
                        pinIndex={pinIndex}
                    />
                ))}
            </GoogleMapReact>
        );
    } else {
        return <span>loading...</span>;
    }
}

function setLocation(setPinIndex, index) {
    setPinIndex(index);
}

const LocationPin = ({ text, index, setPinIndex, pinIndex, name }) => (
    <div className="pin" onClick={() => setLocation(setPinIndex, index)}>
        <Icon
            icon={locationIcon}
            className="pin-icon"
            style={{
                width: "3rem",
                height: "3rem",
                color: "#ff3e3e",
                filter: "drop-shadow(2px 2px 2px #525252)",
            }}
        />
        {pinIndex === index && (
            <p className="pin-text">
                <span
                    style={{
                        display: "inline-block",
                        fontWeight: "700",
                        margin: "0 0 0.5rem 0",
                    }}
                >
                    {name}
                </span>
                {text}
            </p>
        )}
    </div>
);
