import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// @material-ui/icons
// core components
import Header from "components/Header/Header.js";
import Footer from "components/Footer/Footer.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Parallax from "components/Parallax/Parallax.js";
// sections for this page
import HeaderLinks from "components/Header/HeaderLinks.js";
import SectionBasics from "./Sections/SectionBasics.js";
import SectionTabs from "./Sections/SectionTabs.js";
import SectionTypography from "./Sections/SectionTypography.js";
// import IntroSection from "./Sections/IntroSection.js";
import Loading from "./Sections/Loading.js";
// import ContactSection from "./Sections/ContactSection.js";

import styles from "assets/jss/material-kit-react/views/components.js";
import { doctors } from "assets/data/doctors.js";
import { trials } from "assets/data/trials.js";
import logo from "assets/img/logo.png";

const useStyles = makeStyles(styles);

export default function Components(props) {
    const [docList, setDocList] = React.useState(null);
    const [loading, setLoading] = React.useState(false);
    const [pageLength, setPageLength] = React.useState(0);
    const [pageIndex, setPageIndex] = React.useState(0);
    const [searchTerm, setsearchTerm] = React.useState("");
    const [searchArea, setSearchArea] = React.useState("");

    const classes = useStyles();
    const { ...rest } = props;
    React.useEffect(() => document.getElementById('regular').setAttribute('autoComplete', 'off'), []);
    return (
        <div>
            <Header
                brand={logo}
                rightLinks={<HeaderLinks />}
                fixed
                color="transparent"
                changeColorOnScroll={{
                    height: 400,
                    color: "white",
                }}
                {...rest}
            />
            <Parallax className="md-h-100">
                <div className={classes.container}>
                    <GridContainer>
                        <GridItem>
                            <div
                                className={`${classes.brand} md-text-align-center`}
                            >
                                <h1 className={`${classes.title} md-fs-3`}>
                                    Paradigm Shift of Patient Care By Precision Medicine
                                </h1>
                            </div>
                        </GridItem>
                    </GridContainer>
                </div>
            </Parallax>

            <div
                className={classNames(
                    classes.main,
                    classes.mainRaised,
                    "md-m-0"
                )}
            >
                <SectionBasics
                    setLoading={setLoading}
                    setDocList={setDocList}
                    setPageLength={setPageLength}
                    doctorData={doctors}
                    pageIndex={pageIndex}
                    setsearchTerm={setsearchTerm}
                    searchTerm={searchTerm}
                    setSearchArea={setSearchArea}
                    searchArea={searchArea}
                    setPageIndex={setPageIndex}
                />
                {docList && !loading && (
                    <SectionTabs
                        loading={loading}
                        docList={docList}
                        setPageIndex={setPageIndex}
                        pageIndex={pageIndex}
                        pageLength={pageLength}
                        setDocList={setDocList}
                        searchTerm={searchTerm}
                        searchArea={searchArea}
                    />
                )}
                {docList && !loading && (
                    <SectionTypography
                        relates={trials.data}
                        searchTerm={searchTerm}
                    />
                )}
                {/* <IntroSection />
                <ContactSection /> */}
                {loading && <Loading />}
            </div>
            <Footer />
        </div>
    );
}
