import React from "react";
import { makeStyles } from "@material-ui/core/styles";
// import FormControlLabel from "@material-ui/core/FormControlLabel";
// import Checkbox from "@material-ui/core/Checkbox";
// import Check from "@material-ui/icons/Check";
// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import CustomInput from "components/CustomInput/CustomInput.js";

import styles from "assets/jss/material-kit-react/views/componentsSections/basicsStyle.js";

const useStyles = makeStyles(styles);

export default function SectionBasics({
    setDocList,
    setLoading,
    doctorData,
    setPageLength,
    searchTerm,
    setsearchTerm,
    setSearchArea,
    searchArea,
    setPageIndex,
}) {
    const classes = useStyles();
    const [checked, setChecked] = React.useState([24, 22]);
    const [tempSearchTerm, setTempSearchTerm] = React.useState("");
    const [selectedEnabled, setSelectedEnabled] = React.useState("b");
    const [checkedA, setCheckedA] = React.useState(true);
    const [checkedB, setCheckedB] = React.useState(false);

    const termsList = [
        "brca",
        "brca1",
        "brca2",
        "map",
        "mutyh",
        "g6pd",
        "serpina1",
    ];

    const handleToggle = (value) => {
        const currentIndex = checked.indexOf(value);
        const newChecked = [...checked];

        if (currentIndex === -1) {
            newChecked.push(value);
        } else {
            newChecked.splice(currentIndex, 1);
        }
        setChecked(newChecked);
    };

    const onKeyPress = (event) => {
        if (event.which === 13) {
            handleSubmit(event);
        }
    };

    const handleSubmit = (event) => {
        window.location.hash = "";
        event.preventDefault();
        setLoading(true);
        setDocList(null);

        if (termsList.indexOf(searchTerm) === -1) {
            setTimeout(() => {
                setLoading(false);
                window.alert(
                    "Please try BRCA, G6PD, MAP, MUTYH, or SERPINA1 only for the proto type application"
                );
                setTempSearchTerm("");
            }, 1000);
            return false;
        }

        const filteredDoctorData = filteredTrials(doctorData.data, searchTerm);

        const pagenatedDocList = pagenationHandler(filteredDoctorData);
        setPageLength(pagenatedDocList.length);

        //TODO: fetch
        setTimeout(() => {
            setLoading(false);
            setDocList(pagenatedDocList);
            window.location.hash = "#doctors";
            setTempSearchTerm("");
        }, 2500);

        setPageIndex(0);
    };

    const filteredTrials = (doctorData, searchTerm) => {
        if (searchArea.length && searchArea !== "CA") {
            return [];
        }
        return doctorData.filter((doctor) => {
            const parsedTagArr = doctor.tags
                .split(", ")
                .map((elem) => elem.toLowerCase());
            return parsedTagArr.indexOf(searchTerm) !== -1;
        });
    };

    const pagenationHandler = (array, doctorsInOnePage = 4) => {
        const orderedArray = array.sort((a, b) => a.order - b.order);
        const result = orderedArray.reduce((resultArray, item, index) => {
            const chunkIndex = Math.floor(index / doctorsInOnePage);

            if (!resultArray[chunkIndex]) {
                resultArray[chunkIndex] = [];
            }

            resultArray[chunkIndex].push(item);

            return resultArray;
        }, []);
        return result;
    };

    return (
        <div className={classes.sections}>
            <div className={classes.container}>
                <div className={classes.title}></div>
                <div id="inputs">
                    <div className={classes.title}>
                        <h3>
                            Enter Biomarker (Gene/Protein/Variations/Clinical
                            Symptom)
                        </h3>
                    </div>
                    <GridContainer>
                        <form className={classes.form} onSubmit={handleSubmit}>
                            <GridItem xs={12} sm={12} md={10} lg={10}>
                                <CustomInput
                                    id="regular"
                                    autocomplete="off"
                                    inputProps={{
                                        placeholder: "Search",
                                        onChange: (event) => {
                                            const term = event.target.value;
                                            const searchTerm = term
                                                .split(",")[0]
                                                .trim()
                                                .toLowerCase();
                                            const searchArea = term.split(
                                                ","
                                            )[1]
                                                ? term
                                                      .split(",")[1]
                                                      .trim()
                                                      .toUpperCase()
                                                : "";
                                            setsearchTerm(searchTerm);
                                            setSearchArea(searchArea);
                                            setTempSearchTerm(
                                                event.target.value
                                            );
                                        },
                                        value: tempSearchTerm,
                                    }}
                                    formControlProps={{
                                        fullWidth: true,
                                    }}
                                />
                            </GridItem>
                            <GridItem xs={12} sm={12} md={2} lg={2}>
                                <Button
                                    onKeyPress={onKeyPress}
                                    type="submit"
                                    color="primary"
                                    className={`md-100`}
                                >
                                    FIND
                                </Button>
                            </GridItem>
                        </form>
                    </GridContainer>
                    {/* <FormControlLabel
                        control={
                            <Checkbox
                                tabIndex={-1}
                                onClick={() => handleToggle("isDoctorOnly")}
                                checkedIcon={
                                    <Check className={classes.checkedIcon} />
                                }
                                icon={
                                    <Check className={classes.uncheckedIcon} />
                                }
                                classes={{
                                    checked: classes.checked,
                                    root: classes.checkRoot,
                                }}
                            />
                        }
                        classes={{
                            label: classes.label,
                            root: classes.labelRoot,
                        }}
                        label="Doctor only"
                    />
                    <FormControlLabel
                        control={
                            <Checkbox
                                tabIndex={-1}
                                onClick={() => handleToggle("isHospitalOnly")}
                                checkedIcon={
                                    <Check className={classes.checkedIcon} />
                                }
                                icon={
                                    <Check className={classes.uncheckedIcon} />
                                }
                                classes={{
                                    checked: classes.checked,
                                    root: classes.checkRoot,
                                }}
                            />
                        }
                        classes={{
                            label: classes.label,
                            root: classes.labelRoot,
                        }}
                        label="Hospital only"
                    /> */}
                </div>
                <div className={classes.space70} />
            </div>
        </div>
    );
}
