import React from "react";
import classNames from "classnames";
import Geocode from "react-geocode";

import { makeStyles } from "@material-ui/core/styles";

import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Paginations from "components/Pagination/Pagination.js";

import styles from "assets/jss/material-kit-react/views/componentsSections/tabsStyle.js";

import GoogleMaps from "./GoogleMaps";
import DocListCard from "./DocListCard";

const useStyles = makeStyles(styles);

export default function SectionTabs({
    docList,
    loading,
    pageIndex,
    pageLength,
    setPageIndex,
    searchArea,
}) {
    const handlePagination = (pageIndex, pageLength) => {
        if (!pageLength) {
            return [];
        }
        const resultArray = [];
        for (let i = 0; i < pageLength; i++) {
            resultArray.push({ text: i + 1 });
        }
        resultArray[pageIndex].active = true;
        return resultArray;
    };
    function getLatLng(address) {
        return new Promise((resolve, reject) => {
            Geocode.setApiKey("AIzaSyDNTiDyjpRmR_wcSHZYb0j4MEkHTtjWLbM");
            Geocode.fromAddress(address).then(
                (response) => {
                    const { lat, lng } = response.results[0].geometry.location;
                    let result = { lat: null, lng: null };
                    result.lat = lat;
                    result.lng = lng;
                    resolve(result);
                },
                (error) => {
                    reject(error);
                }
            );
        });
    }
    async function createLocationList(currDocList) {
        const promises = currDocList.map((eachDoc) =>
            getLatLng(eachDoc.location)
        );
        try {
            const locationList = await Promise.all(promises);
            locationList.forEach((location, index) => {
                location.location = currDocList[index].location;
                location.name = currDocList[index].name;
            });
            return locationList;
        } catch (e) {
            throw new Error(e);
        }
    }

    const classes = useStyles();
    return (
        <div id="doctors" className={classes.section}>
            <div className={classes.container}>
                {docList && (
                    <div className={classes.title}>
                        <h2>
                            Search Result {searchArea && `in ${searchArea}`}
                        </h2>
                    </div>
                )}
                <div>
                    <GridContainer>
                        <GridItem xs={12} sm={12} md={8}>
                            {docList && docList[pageIndex] ? (
                                docList[pageIndex].map((eachDoc, index) => (
                                    <DocListCard doctor={eachDoc} key={index} />
                                ))
                            ) : (
                                <span>No result</span>
                            )}
                        </GridItem>
                        {docList && docList[pageIndex] && !loading && (
                            <GridItem
                                md={4}
                                className={classNames(
                                    classes.height100,
                                    classes.mdHeight100,
                                    classes.mapMargin
                                )}
                            >
                                <GoogleMaps
                                    getLocationList={createLocationList}
                                    docList={docList}
                                    pageIndex={pageIndex}
                                />
                            </GridItem>
                        )}
                        {docList && !loading && (
                            <GridItem xs={12} sm={12} md={12}>
                                <div className={classes.textCenter}>
                                    <Paginations
                                        pages={handlePagination(
                                            pageIndex,
                                            pageLength
                                        )}
                                        setPageIndex={setPageIndex}
                                    />
                                </div>
                            </GridItem>
                        )}
                    </GridContainer>
                </div>
            </div>
        </div>
    );
}
