import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import ReadMoreReact from "read-more-react";

// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";

import styles from "assets/jss/material-kit-react/views/componentsSections/typographyStyle.js";

const useStyles = makeStyles(styles);

export default function SectionTypography({ relates, searchTerm }) {
    const classes = useStyles();
    const filteredTrials = relates.filter((relate) => {
        const parsedTagArr = relate.tags
            .split(", ")
            .map((elem) => elem.toLowerCase());
        return parsedTagArr.indexOf(searchTerm.toLowerCase()) !== -1;
    });
    return (
        <div className={classes.section}>
            <div className={classes.container}>
                {filteredTrials && !!filteredTrials.length && (
                    <div className={classes.title}>
                        <h2>Clinical Trials</h2>
                    </div>
                )}
                <GridContainer>
                    {filteredTrials &&
                        !!filteredTrials.length &&
                        filteredTrials.map((relate, index) => (
                            <GridItem
                                key={index}
                                sm={12}
                                className={classes.relatesBox}
                            >
                                <div>
                                    <h4>
                                        <b><span>Title: </span>{relate.title}</b>
                                    </h4>
                                    <h4>
                                        <b><span>Contact: </span>{relate.contact}</b>
                                    </h4>
                                    <h4>
                                        <b><span>Main PI and Site: </span>{relate.mainLocation}</b>
                                    </h4>
                                    <ReadMoreReact
                                        text={relate.description}
                                        min={400}
                                        ideal={500}
                                        max={600}
                                        readMoreText="...read more"
                                    />
                                    <h4 className={classes.linkBox}>
                                        <a
                                            href={relate.link}
                                            target="_blank"
                                            rel="noopener noreferrer"
                                        >
                                            Link
                                        </a>
                                    </h4>
                                </div>
                            </GridItem>
                        ))}
                </GridContainer>
            </div>
        </div>
    );
}
